.nav {
  margin-bottom: 38px;

  &__list {
    gap: 37px;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
  }

  &__descr {
    color: var(--light-color);
    text-align: center;
    font-size: 12px;
    font-weight: 600;
    line-height: 14px;
    letter-spacing: 3px;
  }
}
