.shop {
  position: relative;

  min-height: 1200px;

  &:before {
    position: absolute;
    content: url(../img/shop-bg-2.png);

    left: 0;
    top: 59px;
  }

  &__title {
    position: relative;

    line-height: 140px;

    width: max-content;

    padding-left: 108px;

    margin-bottom: 33px;

    &:before {
      position: absolute;
      content: url(../img/surf-section-divide.svg);

      top: -71px;
      right: 0;
    }
  }

  &-navigation {
    position: absolute;

    top: 0;
    right: 80px;

    gap: 20px;

    z-index: 10;
  }

  &__slider {
    position: absolute;
    left: 0;

    min-height: 1100px;

    width: 100%;

    padding-top: 18px;
  }

  &__btn {
    position: absolute;
    left: 194px;
    bottom: 25px;
  }
}

.shop-slide {
  position: relative;

  min-height: 1100px;
  width: 100%;

  &__image {
    width: 1098px;

    object-fit: cover;

    position: absolute;

    top: 0;
    right: 0;

    opacity: .95;

    padding-top: 50px;
  }

  &__content {
    position: relative;
  }

  &__left {
    position: absolute;
    left: 133px;

    padding-top: 151px;
  }

  &__caption {
    margin: 0 0 14px;

    color: var(--light-color);
    font-size: 22px;
    line-height: 32px;
    letter-spacing: 7.333px;
    text-transform: uppercase;
    font-weight: 300;

    opacity: .5;

    position: relative;

    &:after {
      position: absolute;
      content: url(../img/travel-line.svg);

      top: -8px;
      left: 119px;
    }
  }

  &__text {
    display: block;

    margin-bottom: 70px;

    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 40px;
    text-transform: uppercase;

    hyphens: auto;

    max-width: 481px;
  }

  &__rating {
    position: relative;

    margin-bottom: 85px;
  }

  &__pic {
    position: absolute;

    top: -43px;
    left: -15px;
  }

  &__price {
    color: var(--light-color);
    font-size: 32px;
    font-weight: 800;
    line-height: 40px;
    text-transform: uppercase;

    display: block;

    padding-top: 4px;
  }

  &__coins {
    font-size: 18px;
    font-weight: 800;
    line-height: 40px;

    position: relative;
    left: 5px;
    bottom: 9px;
  }

  &__drop {
    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 36px;
    text-transform: uppercase;
    word-spacing: 27px;

    position: relative;

    width: max-content;

    &:before {
      position: absolute;
      content: url(../img/surf-card-dash.svg);

      width: 20px;
      height: 1px;

      top: -12px;
      left: 114px;

      z-index: 2;
    }

    &:after {
      position: absolute;
      content: url(../img/shop-rect.svg);

      right: -58px;
      bottom: -26px;

      z-index: -1;
    }
  }

  &__picture {
    position: relative;

    width: 228px;
    height: 695px;

    left: 53.1%;
    transform: translateX(-49.3%);
  }

  &__img {
    position: absolute;

    width: 228px;
    height: 695px;
  }

  &__extras {
    position: absolute;
    top: 432px;
    right: 58px;

    min-width: 350px;
  }

  &__subtitle {
    margin: 0 0 28px;

    color: var(--light-color);
    font-size: 28px;
    font-weight: 800;
    line-height: 40px;
    text-transform: uppercase;
  }

  &__list {
    gap: 57px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-left: 5px;
  }

  &__logo {
    &--one {
      width: 106px;
      height: 108px;

      margin-bottom: 35px;
    }

    &--two {
      padding-top: 15px;

      width: 120px;
      height: 81px;

      margin-bottom: 45px;
      margin-left: -10px;
    }
  }

  &__heading {
    display: block;

    color: var(--light-color);
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 8px;
    text-transform: uppercase;

    margin-bottom: 5px;
    margin-left: -3px;

    opacity: .5;
  }

  &__cost {
    color: var(--light-color);
    text-align: center;
    font-size: 18px;
    font-weight: 800;
    line-height: 40px;
    text-transform: uppercase;
  }
}

.shop-point {
  width: 20px;
  height: 20px;

  position: absolute;
  top: 203px;
  right: 36px;

  z-index: 5;

  &:nth-child(2) {
    top: 296px;
    left: -6px;
  }

  &:nth-child(3) {
    top: 611px;
    left: 52px;
  }

  &__img {
    width: 20px;
    height: 20px;

    position: absolute;
    left: 0;

    cursor: pointer;

    &:hover {
      content: url(../img/shop-minus.svg);
    }

    &:hover ~ .shop-point__line {
      visibility: visible;
      opacity: 1;
    }

    &:hover ~ .shop-point__info {
      visibility: visible;
      opacity: 1;
    }
  }

  &__line {
    position: relative;
    top: -4px;
    left: 27px;

    min-width: 80px;
    height: 1px;

    visibility: hidden;
    opacity: 0;

    transition: opacity .5s ease-in-out;
  }

  &__info {
    min-width: 255px;

    position: absolute;
    top: -15px;
    left: 131px;

    visibility: hidden;
    opacity: 0;

    transition: opacity .5s ease-in-out;
  }

  &__descr {
    margin: 0;

    color: var(--light-color);
    font-size: 24px;
    font-weight: 800;
    line-height: 24px;
    text-transform: uppercase;
  }
}

.shop-slide__picture > .shop-point:nth-child(2) > .shop-point__line, .shop-slide__picture > .shop-point:nth-child(3) > .shop-point__line {
  left: -95px;
}

.shop-slide__picture > .shop-point:nth-child(2) > .shop-point__info, .shop-slide__picture > .shop-point:nth-child(3) > .shop-point__info {
  left: -380px;
  top: -20px;

  max-width: 280px;
}

.shop-slide__list > .shop-slide__item:nth-child(2) > .shop-slide__heading {
  margin-bottom: 7px;
}
