.travel {
  position: relative;

  background: #171919;

  min-height: 1718px;

  &:before {
    position: absolute;
    content: url(../img/travel-bg-1.png);

    left: 0;
    top: 73px;
  }

  &:after {
    position: absolute;
    content: url(../img/travel-bg-2.png);

    right: 0;
    bottom: 60px;
  }

  &__title {
    position: relative;

    line-height: 140px;

    width: max-content;

    padding-left: 108px;

    margin-bottom: 90px;

    &:before {
      position: absolute;
      content: url(../img/surf-section-divide.svg);

      top: -69px;
      right: 0;
    }
  }
}

.travel__slider {
  position: absolute;

  min-height: 1380px;
  width: 100%;

  left: 0;
}

.travel-slide {
  position: relative;

  min-height: 1324px;
  width: 100%;

  &__image {
    width: 1113px;

    object-fit: cover;

    position: absolute;

    left: 0;
    top: -82px;

    background-color: #171919;

    padding-top: 50px;
  }

  &__top {
    position: absolute;

    top: 58px;
    right: 84px;
  }

  &__subtitle {
    margin: 0;

    color: var(--light-color);
    text-align: right;
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 7.333px;
    text-transform: uppercase;

    opacity: .5;

    position: relative;

    transform: translateX(7px);

    &:before {
      position: absolute;

      content: url(../img/travel-line.svg);

      right: 128px;
      bottom: 8px;
    }

    &--bottom {
      text-align: left;

      margin-left: -6px;

      &:after {
        position: absolute;

        content: url(../img/travel-line.svg);

        right: 177px;
        bottom: 8px;
      }

      &:before {
        content: none;
      }
    }
  }

  &__descr {
    color: var(--light-color);
    text-align: right;
    font-size: 40px;
    font-weight: 800;
    line-height: 69px;
    text-transform: uppercase;
  }

  &__dash {
    font-weight: 250;
  }

  &__bottom {
    position: absolute;

    left: 132px;
    top: 644px;
  }

  &__img {
    width: 52px;
    height: 46px;

    object-fit: cover;

    padding-left: 22px;

    transform: translateY(7px);
  }

  &__pic {
    position: absolute;

    right: 0;
    bottom: 491px;
  }

  &__footer {
    position: absolute;

    bottom: 397px;

    width: 100%;

    padding: 0 172px;

    box-sizing: border-box;
  }

  &__list {
    justify-content: space-between;

    @include big-desktop {
      justify-content: flex-start;
      gap: 99px;
    }
  }

  &__item {
    position: relative;
  }

  &__caption {
    margin: 0;

    transform: rotate(-90deg);

    color: var(--light-color);
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 8px;
    text-transform: uppercase;
    text-align: right;

    opacity: 0.5;

    position: absolute;

    top: 82px;
    left: -125px;

    width: 190px;
  }

  &__text {
    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 36px;
    text-transform: uppercase;

    display: block;

    max-width: 242px;
  }

  &__string {
    color: var(--light-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 5.25px;
    text-transform: uppercase;

    opacity: 0.75;

    position: absolute;
    right: -7px;
  }

  &__booking {
    position: absolute;
    bottom: 158px;

    width: 100%;

    align-items: center;
    gap: 16px;

    transform: translateX(-12px);

    &:after {
      position: absolute;
      content: url(../img/surf-card-dash.svg);

      width: 20px;
      height: 1px;

      top: 0;
      left: 47.5%;

      z-index: 2;
    }
  }

  &__book {
    position: relative;

    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 28px;
    text-transform: uppercase;
    word-spacing: 26px;

    display: block;

    padding-left: 4px;

    z-index: 1;

    &:after {
      position: absolute;
      content: url(../img/travel-rect.svg);

      width: 201px;
      height: 80px;

      left: 128px;
      top: -27px;

      z-index: -1;
    }
  }
}

.travel-navigation {
  position: absolute;

  right: 81px;
  top: 176px;

  gap: 20px;
}

.travel-slide__list > .travel-slide__item:nth-child(even) > .travel-slide__text {
  white-space: nowrap;
}

.travel-slide__list > .travel-slide__item:nth-child(3) > .travel-slide__caption, .travel-slide__list > .travel-slide__item:nth-child(4) > .travel-slide__caption {
  margin-left: -11px;
}

.travel-slide__list > .travel-slide__item:nth-child(3) > .travel-slide__text {
  margin-left: -11px;
}

.travel-slide__list > .travel-slide__item:nth-child(4) > .travel-slide__text {
  margin-top: 4px;
  margin-left: -9px;
}
