.hero {
  min-height: 1024px;

  &__slider {
    z-index: 4;
  }

  &__slide {
    height: 1024px;

    position: relative;
  }

  &__image {
    width: 100%;

    object-fit: cover;
  }

  &__map {
    position: absolute;

    right: 0;
    top: 0;
  }

  &__content {
    position: absolute;

    top: 613px;
    right: 80px;

    max-width: 515px;
  }

  &__title {
    color: rgba(255, 255, 255, 0.5);
    text-align: right;
    font-size: 22px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 7.333px;
    text-transform: uppercase;

    position: relative;

    margin: 0 0 5px 0;

    transform: translateX(7px);

    &:before {
      position: absolute;
      content: url(../img/hero-line.svg);
      right: 104px;
      top: -8px;
    }

    &--bottom {
      margin-bottom: 8px;

      &:before {
        position: absolute;
        content: url(../img/hero-line.svg);
        right: 200px;
        top: -7px;
      }
    }
  }

  &__descr {
    color: var(--light-color);
    text-align: right;
    font-size: 80px;
    font-weight: 800;
    line-height: 113px;
    text-transform: uppercase;

    display: block;

    margin-bottom: 31px;

    &--bottom {
      font-size: 40px;

      margin-bottom: 10px;

      line-height: 67px;
    }
  }

  &__bottom-content {
    width: 100%;

    padding-left: 209px;
    padding-right: 80px;

    box-sizing: border-box;

    position: absolute;

    bottom: 21px;

    justify-content: space-between;
  }
}

.hero-navigation {
  position: absolute;

  &__top {
    right: 80px;
    top: 40px;

    gap: 20px;

    z-index: 1;
  }

  &__middle {
    width: 100%;

    justify-content: flex-end;
  }

  &__bottom {
    position: static;
  }

  &__list {
    gap: 40px;

    transform: translate(1px, -13px);
  }

  &__item {
    min-width: 200px;

    color: var(--light-color);

    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 87px;

    padding-top: 3px;

    box-sizing: border-box;

    border-top: 4px solid var(--light-color);

    cursor: pointer;

    &--active {
      border-top-color: var(--aquamarine-color);
    }
  }

  &__num {
    font-size: 24px;
    font-weight: 800;
    line-height: 34px;
    text-transform: uppercase;
  }

  &__descr {
    text-align: right;
    font-size: 16px;
    font-weight: 300;
    line-height: 19px;

    white-space: nowrap;
  }
}

.btn-down {
  width: 20px;
  height: 24px;

  &__icon {
    display: block;

    width: 20px;
    height: 24px;

    fill: var(--light-color);

    cursor: pointer;
  }
}
