.surf {
  background-color: #171919;

  padding-top: 160px;

  &__top {
    position: relative;

    min-height: 930px;

    grid-template-columns: 432px 1fr 490px;
    grid-template-areas:
    "surf-title . surf-location"
    ". surf-map .";

    margin-bottom: 60px;

    @include big-desktop {
      min-height: 839px;
    }

    &:before {
      position: absolute;
      content: url(../img/surf-section-logo.svg);

      left: 0;
      top: 33px;
    }

    &:after {
      position: absolute;
      content: url(../img/surf-section-location.svg);

      right: 0;
      top: 59px;
    }
  }

  &__title {
    grid-area: surf-title;
    justify-self: end;

    margin: 0;

    color: var(--light-color);
    font-size: 80px;
    font-weight: 800;
    line-height: 90px;
    text-transform: uppercase;

    position: relative;

    width: fit-content;

    &:before {
      position: absolute;
      content: url(../img/surf-section-divide.svg);

      top: -71px;
      right: 0;
    }
  }

  &__location {
    grid-area: surf-location;

    flex-direction: column;

    max-width: 417px;

    padding-top: 149px;
  }

  &__subtitle {
    position: relative;

    margin: 0;

    color: var(--light-color);
    text-align: right;
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 7.333px;
    text-transform: uppercase;

    opacity: .5;

    &:before {
      position: absolute;
      content: url(../img/hero-line.svg);

      left: 0;
      top: -8px;
    }
  }

  &__descr {
    color: var(--light-color);
    text-align: right;
    font-size: 40px;
    font-weight: 800;
    line-height: 68px;
    text-transform: uppercase;

    padding-right: 7px;

    &--line {
      font-weight: 250;
    }
  }

  &__map {
    position: absolute;

    top: 207px;

    width: 1400px;

    left: 50%;

    transform: translateX(-50%);

    &:before {
      position: absolute;
      content: url(../img/surf-map-location.svg);

      top: 312px;
      left: 194px;
    }

    &:after {
      position: absolute;
      content: url(../img/surf-coordinates.svg);

      top: 219px;
      right: -44px;
    }

    @include big-desktop {
      &:before {
        top: 268px;
        left: 238px;
      }

      &:after {
        top: 171px;
        right: 56px;
      }
    }
  }

  &__image {
    width: 1600px;

    position: absolute;
    left: 50%;

    transform: translateX(-50%);

    @include big-desktop {
      top: 0;
      left: 74px;

      width: 1200px;
      height: 634px;

      transform: none;
    }
  }

  &__list {
    position: relative;
  }

  &__item {
    position: absolute;

    // display: flex;
    // align-items: center;
    // justify-content: center;

    width: 24px;
    height: 24px;

    cursor: pointer;

    &:hover {
      width: 25px;
    }

    &:hover > .surf-info {
      visibility: visible;
      opacity: 1;
    }

    &--one {
      top: 386px;
      left: -13px;
    }

    &--two {
      top: 341px;
      left: 158px;
    }

    &--three {
      top: 374px;
      left: 387px;
    }

    &--four {
      top: 584px;
      left: 479px;
    }

    &--five {
      top: 253px;
      left: 623px;
    }

    &--six {
      top: 653px;
      left: 743px;
    }

    &--seven {
      top: 454px;
      left: 944px;
    }

    &--eight {
      top: 579px;
      left: 1268px;
    }

    @include big-desktop {
      &--one {
        top: 330px;
        left: 59px;
      }

      &--two {
        top: 290px;
        left: 208px;
      }

      &--three {
        top: 318px;
        left: 410px;
      }

      &--four {
        top: 501px;
        left: 485px;
      }

      &--five {
        top: 212px;
        left: 602px;
      }

      &--six {
        top: 555px;
        left: 709px;
      }

      &--seven {
        top: 395px;
        left: 870px;
      }

      &--eight {
        top: 501px;
        left: 1161px;
      }
    }
  }

  &__icon {
    fill: var(--aquamarine-color);
    filter: drop-shadow(0 0 20px rgba(74, 246, 205, 0.84));

    width: 24px;
    height: 24px;

    &:hover {
      content: url(../img/surf-map-location-active.svg);

      width: 25px;
      height: 34px;
    }

    &:hover ~ .surf-info {
      visibility: visible;
      opacity: 1;
    }
  }
}

.surf-info {
  margin-bottom: 50px;

  min-width: 283px;
  max-width: 283px;

  max-height: 216px;

  box-sizing: border-box;

  padding: 28px 32px 31px;

  border-radius: 6px;

  background: rgba(15, 16, 16, 0.7);

  visibility: hidden;

  opacity: 0;

  position: absolute;

  left: 50%;

  transform: translate(-50%, -210px);

  transition: opacity .5s ease-in-out;

  &:hover ~ .surf__icon {
    content: url(../img/surf-map-location-active.svg);
    width: 25px;
    height: 34px;
  }

  &__top {
    gap: 90px;

    margin-bottom: 9px;
  }

  &__headline {
    margin: 0;

    white-space: nowrap;

    color: var(--light-color);
    font-size: 18px;
    font-weight: 800;
    line-height: 20px;
    text-transform: uppercase;

    padding-left: 6px;
  }

  &__btn {
    width: 16px;
    height: 13px;

    align-self: flex-start;
    padding-top: 1px;

    img {
      width: 16px;
      height: 13px;
    }
  }

  &__icon {
    width: 16px;
    height: 13px;

    fill: var(--light-color);
  }

  &__descr {
    width: 80px;

    display: block;

    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 11px;

    padding-top: 13px;

    border-top: 1px solid var(--aquamarine-color);

    margin-bottom: 33px;

    padding-left: 3px;
  }

  &__list {
    gap: 37px;

    padding-left: 12px;
  }

  &__item {
    flex-direction: column;

    max-width: 59px;
  }

  &__image {
    margin-bottom: 6px;
  }

  &__value {
    color: var(--light-color);
    font-size: 16px;
    font-weight: 800;
    line-height: 23px;
    white-space: nowrap;

    &--one {
      letter-spacing: -1.4px;
    }
  }

  &__text {
    color: var(--light-color);
    font-size: 10px;
    font-weight: 300;
    line-height: 12px;
    text-transform: uppercase;
  }
}

.surf__bottom {
  padding: 0 100px;

  @include big-desktop {
    padding: 0;
  }
}

.surf-card {
  max-height: 750px;

  padding-top: 77px;
  padding-bottom: 345px;

  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: multiply;

  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

  background-color: hsla(180, 8%, 12%, 0.851);

  cursor: pointer;

  &--active {
    background-color: hsla(180, 8%, 12%, 0.315);

    &:after {
      position: absolute;
      content: url(../img/surf-card-rect.svg);

      height: 80px;

      bottom: 1px;
      left: 154px;

      z-index: 0;
    }

    & > .surf-card__bottom {
      opacity: 1;
    }
  }

  &--one {
    background-image: url(../img/surf-card-1.jpg);
  }

  &--two {
    background-image: url(../img/surf-card-2.jpg);
  }

  &--three {
    background-image: url(../img/surf-card-3.jpg);
  }

  &--four {
    background-image: url(../img/surf-card-4.jpg);
  }

  &__title {
    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 37px;
    text-transform: uppercase;

    padding-left: 40px;
    padding-right: 200px;

    margin: 0 0 254px;

    background: none;

    &:after {
      content: '';
    }
  }

  &__descr {
    color: var(--light-color);
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 8px;
    text-transform: uppercase;

    transform: rotate(-90deg);

    display: block;

    position: absolute;

    left: -146px;
    bottom: 313px;

    background: none;

    width: 100%;

    &:nth-child(even) {
      left: 50px;
    }

    &:after {
      content: '';

      position: static;
    }
  }

  &__divide {
    font-weight: 250;
  }

  &__bottom {
    position: absolute;
    bottom: 0;

    width: 100%;

    margin-bottom: 21px;

    gap: 16px;

    padding-left: 45px;

    margin-top: 140px;

    opacity: 0;

    @include big-desktop {
      margin-top: 0;
    }
  }

  &__text {
    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 36px;
    text-transform: uppercase;
    word-spacing: 28px;

    position: relative;

    z-index: 1;

    &:before {
      position: absolute;
      content: url(../img/surf-card-dash.svg);

      bottom: 14px;
      left: 99px;
    }
  }

  &__btn {
    width: 24px;
    height: 20px;

    z-index: 1;
  }

  &__icon {
    fill: var(--light-color);

    width: 24px;
    height: 20px;
  }
}

.surf-cards__slider {
  padding-top: 65px;
  padding-bottom: 320px;
}

.surf-cards__slide:nth-child(odd) {
  min-width: 390px;
  max-width: 390px;
}

.surf-cards__slide:nth-child(even) {
  margin-left: -40px;

  min-width: 400px;
  max-width: 400px;

  position: relative;
  bottom: 32px;
}

.surf-cards__slide:nth-child(3n) {
  top: 57px;

  margin-left: -50px;

  .surf-card__descr {
    bottom: 266px;
    left: -145px;
  }
}

.surf-cards__slide:nth-child(5n) {
  margin-left: -50px;
}

.surf-card--odd > .surf-card__descr {
  left: -146px;
  bottom: 265px;
}

.surf-card--even > .surf-card__descr {
  left: -151px;
  bottom: 270px;
}

.surf-navigation {
  justify-content: end;
  gap: 20px;

  padding-right: 80px;
}
