.sleep {
  position: relative;

  min-height: 1710px;

  &:before {
    position: absolute;
    content: url(../img/sleep-bg-3.png);

    left: 0;
    top: 59px;
  }

  &:after {
    position: absolute;
    content: url(../img/sleep-bg-2.png);

    right: 0;
    bottom: 242px;
  }

  &__title {
    position: relative;

    line-height: 140px;

    width: max-content;

    padding-left: 108px;

    margin-bottom: 90px;

    &:before {
      position: absolute;
      content: url(../img/surf-section-divide.svg);

      top: -71px;
      right: 0;
    }
  }
}

.sleep__slider {
  position: absolute;

  min-height: 1380px;
  width: 100%;

  left: 0;
}

.sleep-slide {
  position: relative;

  min-height: 1324px;
  width: 100%;

  &__image {
    width: 1098px;

    object-fit: cover;

    position: absolute;

    left: 0;
    top: -71px;

    opacity: .95;

    padding-top: 50px;
  }

  &__top {
    position: absolute;

    top: 58px;
    right: 84px;
  }

  &__subtitle {
    margin: 0;

    color: var(--light-color);
    text-align: right;
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 7.333px;
    text-transform: uppercase;

    opacity: .5;

    position: relative;

    transform: translateX(7px);

    &:before {
      position: absolute;

      content: url(../img/travel-line.svg);

      right: 163px;
      bottom: 8px;
    }

    &--bottom {
      text-align: left;

      margin-left: -6px;

      &:after {
        position: absolute;

        content: url(../img/travel-line.svg);

        right: -14px;
        bottom: 8px;
      }

      &:before {
        content: none;
      }
    }
  }

  &__descr {
    color: var(--light-color);
    text-align: right;
    font-size: 40px;
    font-weight: 800;
    line-height: 69px;
    text-transform: uppercase;
  }

  &__dash {
    font-weight: 250;
  }

  &__bottom {
    position: absolute;

    left: 132px;
    top: 649px;
  }

  &__img {
    position: absolute;
    right: -197px;

    object-fit: cover;

    transform: translateY(7px);
  }

  &__pic {
    position: absolute;

    right: 0;
    bottom: 439px;
  }

  &__footer {
    position: absolute;

    bottom: 392px;

    width: 100%;

    padding: 0 172px;

    box-sizing: border-box;
  }

  &__list {
    justify-content: space-between;

    @include big-desktop {
      justify-content: flex-start;
      gap: 99px;
    }
  }

  &__item {
    position: relative;
  }

  &__caption {
    margin: 0;

    transform: rotate(-90deg);

    color: var(--light-color);
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 8px;
    text-transform: uppercase;
    text-align: right;

    opacity: 0.5;

    position: absolute;

    top: 82px;
    left: -125px;

    width: 190px;
  }

  &__text {
    color: var(--light-color);
    font-weight: 800;
    line-height: 36px;
    text-transform: uppercase;

    display: block;

    max-width: 242px;

    font-size: 34px;
    letter-spacing: 6px;
  }

  &__string {
    color: var(--light-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 5.25px;
    text-transform: uppercase;

    opacity: 0.75;

    position: absolute;
    right: -71px;
  }

  &__booking {
    position: absolute;
    bottom: 153px;

    width: 100%;

    align-items: center;
    gap: 24px;

    transform: translateX(-12px);

    &:after {
      position: absolute;
      content: url(../img/surf-card-dash.svg);

      width: 20px;
      height: 1px;

      top: 0;
      left: 48.5%;

      z-index: 2;
    }
  }

  &__book {
    position: relative;

    color: var(--light-color);
    font-size: 40px;
    font-weight: 800;
    line-height: 28px;
    text-transform: uppercase;
    word-spacing: 26px;

    display: block;

    padding-left: 8px;

    z-index: 1;

    &:after {
      position: absolute;
      content: url(../img/surf-card-rect.svg);

      width: 180px;
      height: 80px;

      left: 133px;
      top: -27px;


      z-index: -1;
    }
  }

  &__symbol {
    font-size: 36px;

    margin-right: 5px;
  }
}

.sleep-navigation {
  position: absolute;

  right: 81px;
  top: 176px;

  gap: 20px;
}

.sleep-slide__list > .sleep-slide__item:nth-child(even) > .sleep-slide__text {
  white-space: nowrap;
}

.sleep-slide__list > .sleep-slide__item:nth-child(1) > .sleep-slide__text {
  font-size: 40px;

  letter-spacing: normal;
}

.sleep-slide__list > .sleep-slide__item:nth-child(3) > .sleep-slide__caption, .sleep-slide__list > .sleep-slide__item:nth-child(4) > .sleep-slide__caption {
  margin-left: 24px;
}

.sleep-slide__list > .sleep-slide__item:nth-child(2) > .sleep-slide__text {
  position: relative;

  margin-top: 2px;

  &:after {
    position: absolute;
    content: url(../img/sleep-pluses-minuses.svg);

    top: 29px;
    right: 10px;
  }
}

.sleep-slide__list > .sleep-slide__item:nth-child(3) > .sleep-slide__text {
  position: relative;

  margin-top: 2px;
  margin-left: -11px;

  transform: translateX(33px);

  &:after {
    position: absolute;
    content: url(../img/sleep-pluses-minuses.svg);

    top: 29px;
    right: 9px;
  }
}

.sleep-slide__list > .sleep-slide__item:nth-child(4) > .sleep-slide__caption {
  transform: rotate(-90deg) translateY(17px);
}

.sleep-slide__list > .sleep-slide__item:nth-child(4) {
  max-width: 150px;
}

.sleep-slide__list > .sleep-slide__item:nth-child(4) > .sleep-slide__text {
  margin-top: 4px;
  margin-left: -9px;

  transform: translateX(48px);

  word-spacing: 1px;
  font-size: 40px;

  letter-spacing: normal;
}
